* {
  text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(
    0,
    0,
    0,
    0
  ); // Remove tap highlight color - ugly!
  -webkit-tap-highlight-color: transparent; // For some Androids
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  /* the slickest text out there */
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: $font-family-sans-serif;
}

// Links
a {
  transition: all 0.2s ease-in-out;
  &:hover {
    opacity: 1;
    color: darken($primary1, 10%);
  }
  &.accent {
    word-wrap: break-word;
    &:hover {
      color: darken($primary1, 10%);
    }
  }
  &:focus {
    outline: 0;
  }
}

// Button Overrides: Required because of hard-coded values in Bootstrap Alpha 6 Button Mixins

button span:focus {
  outline: 0;
}

.btn-outline-secondary:hover {
  color: $primary1;
}

.btn-secondary:hover {
  color: $link-hover-color;
}

.btn-primary:focus,
.btn-outline-primary:focus,
.btn-outline-secondary:focus {
  outline: -webkit-focus-ring-color auto 5px;
}

.btn-primary.disabled,
a.disabled.btn-primary,
.btn-primary:disabled,
a.btn-primary:disabled {
  background-color: $gray;
  border-color: $gray;
  color: $footer-gray2;
}

.close {
  color: $primary3;
  opacity: 1;
  font-size: 2rem;
  font-weight: 200;
  line-height: 1;
  text-shadow: none;
  transition: $transition-base;
  &.black {
    color: $primary2;
    font-size: 1.5rem;
  }
  &.black-modal {
    color: $primary2;
  }
}

.btn {
  border-width: 2px;
  font-family: $font-family-sans-serif;
  cursor: pointer;
  &.btn-tertiary {
    color: $btn-tertiary-color;
    background-color: $btn-tertiary-bg;
    border-color: $btn-tertiary-border;
    cursor: pointer;
    width: 100px;
    &:hover {
      background: #cfd6db;
      transition: all 0.2s ease-in-out;
    }
  }
  &.btn-quaternary {
    color: $primary3;
    background-color: $tertiary7;
    border: 1px solid $primary3;
    width: 100px;
    &:hover {
      background: #cfd6db;
      transition: all 0.2s ease-in-out;
      color: $primary2;
    }
  }
  &.search {
    padding: 0.5rem 0;
    @include media-breakpoint-only(md) {
      padding: 1rem 2rem;
    }
    @include media-breakpoint-only(lg) {
      padding: 1rem 3rem;
    }
    @include media-breakpoint-only(xl) {
      padding: 1rem 4rem;
    }
    border-radius: 5px;
    border: solid 2px $tertiary5;
    background-color: $primary3;
    &:hover,
    &.active {
      border-color: $primary1;
      background-color: $primary1;
      color: $primary3;
    }
  }
}

// Dropdowns
.dropdown-menu {
  box-shadow: 0 11px 20px 0 rgba(0, 0, 0, 0.3);
}

// Kill Bootstrap Gutters - Recommended by BS, only for rows
.no-xs-guts {
  @include media-breakpoint-down(xs) {
    margin-right: 0;
    margin-left: 0;
    padding-right: 0;
    padding-left: 0;
    > .col,
    > [class*="col-"] {
      padding-right: 0;
      padding-left: 0;
    }
  }
}

// Carousel Indicators
.carousel-indicators li {
  border-radius: 6px;
}

.nowrap {
  white-space: nowrap;
}
.word-wrap {
  word-wrap: break-word;
}

.phone-link {
  &:before {
    content: "";
    display: inline-block;
    width: 22px;
    height: 22px;
    background-image: url("../../assets/images/icon-phone.svg");
    margin-right: 7px;
    vertical-align: middle;
  }
}

.email-link {
  &:before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 15px;
    background-image: url("../../assets/images/icon-email.svg");
    margin-right: 7px;
    vertical-align: middle;
  }
}

.profile-img {
  width: 136px;
  border-radius: 100%;
  @include media-breakpoint-down(sm) {
    width: 120px;
  }
}

.profile-img-alt {
  width: 90px;
  border-radius: 100%;
  @include media-breakpoint-down(md) {
    width: 78px;
  }
}

.profile-img-standard {
  width: 56px;
  height: 56px;
  min-width: 56px;
  min-height: 56px;
  border-radius: 100%;
  margin: 0px;
  padding: 0px;
}

.profile-img-leader {
  width: 120px;
  height: 120px;
  border-radius: 100%;
}

.circular {
  border-radius: 100%;
}

/* Source: https://css-tricks.com/NetMag/FluidWidthVideo/Article-FluidWidthVideo.php */
.video-player {
  position: relative;
  padding-bottom: 54%;
  /*56.25%; */
  /* 16:9 */
  padding-top: 25px;
  height: 0;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.img-shadow {
  box-shadow: 0 25px 40px 0 rgba(0, 0, 0, 0.5);
}

.border-after {
  &:after {
    content: "";
    display: block;
    width: 100%;
    border-bottom: 2px solid $tertiary4;
    margin: 3rem 0 0;
  }
  &.border-after__thin {
    &:after {
      border-bottom-width: 1px;
      margin: 1rem 0;
    }
  }
  &.border-after__modal {
    @include media-breakpoint-down(md) {
      &:after {
        border-bottom-width: 0;
      }
    }
  }
}

.border-before {
  &:before {
    content: "";
    display: block;
    width: 100%;
    border-top: 2px solid $tertiary4;
    margin: 3rem 0 0;
  }
  &.border-after__thin {
    &:before {
      border-top-width: 1px;
      margin: 1rem 0;
    }
  }
  &.border-after__modal {
    @include media-breakpoint-down(md) {
      &:before {
        border-top-width: 0;
      }
    }
  }
}

// Crazy Hidden Class, Not in Bootstrap Alpha 4
.hidden-md {
  @include media-breakpoint-only(md) {
    display: none;
  }
}

// Share Save & ToolTips

.share-bar {
  &.share-bar--small {
    img.share-bar__icon {
      height: 20px;
    }
  }
}

.tooltip {
  font-size: 16px;
  .tooltip-inner {
    border-radius: 5px;
  }
  img {
    height: 18px;
    margin: 0 7px;
  }
}

.share {
  z-index: 100;
  &.share--cart {
    width: 43px; //27
    height: 24px;
    background-image: url("../../assets/images/cart.svg");
    background-repeat: no-repeat;
    &:hover {
      background-image: url("../../assets/images/cart.svg");
    }
    &.active {
      background-image: url("../../assets/images/cart.svg");
    }
  }
  &.share--facebook {
    width: 38px;
    height: 15px;
    background-image: url("../../assets/images/icon-share.svg");
    background-repeat: no-repeat;
    &:hover {
      background-image: url("../../assets/images/icon-share-dark.svg");
    }
    &.active {
      background-image: url("../../assets/images/icon-share-accent.svg");
    }
  }
  &.share--save {
    width: 38px;
    height: 20px;
    background-image: url("../../assets/images/icon-save.svg");
    background-repeat: no-repeat;
    &:hover {
      background-image: url("../../assets/images/icon-save-dark.svg");
    }
    &.active {
      background-image: url("../../assets/images/icon-save-accent.svg");
    }
  }
}

//IE bug fix on all carousels
.carousel-item {
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    opacity: 0;
    transition: opacity 0.75s ease;
    &.active {
      opacity: 1;
    }
  }
}

//ipad bug fix on all carousels
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .carousel-item {
    transition: width 0.5s ease;
  }
}

.btn-light {
  //datepicker dates
  color: #333 !important;
}

.popover {
  background: transparent !important;
  top: 5px !important;
  left: -8px !important;
}

.popover-title {
  padding: 6px !important;
  border-radius: 6px;
  background-color: #333 !important;
  .bg-gray-dark {
    background-color: #333 !important;
  }
}
.bg-gray-dark {
  background-color: #333;
}

.share-img {
  width: 10px;
  height: 10px;
}

.popover-content {
  border-radius: 0 !important;
}
.popover a:hover {
  color: $primary3;
}

.popover.popover-bottom::before,
.popover.bs-tether-element-attached-top::before {
  border-bottom-color: transparent !important;
  border-left-color: transparent !important;
  border-right-color: transparent !important;
  border-top-color: transparent !important;
}

$carousel-control-prev-icon-icsc: str-replace(
  url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' stroke='#000' stroke-width='.25px' fill='#{$carousel-control-color}' viewBox='0 0 8 8'%3E%3Cpath d='M4 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E"),
  "#",
  "%23"
) !default;
$carousel-control-next-icon-icsc: str-replace(
  url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' stroke='#000' stroke-width='.25px' fill='#{$carousel-control-color}' viewBox='0 0 8 8'%3E%3Cpath d='M1.5 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E"),
  "#",
  "%23"
) !default;

.carousel-control-prev-icon {
  background-image: $carousel-control-prev-icon-icsc !important;
}
.carousel-control-next-icon {
  background-image: $carousel-control-next-icon-icsc !important;
}

@media only screen and (max-device-width: 459px) {
  .table th,
  .table td {
    padding: 0.15rem !important;
  }
} //end query

