
.short-hero {
  background-position: center center; 
  background-size: cover;
  position: relative; 
  &:before {
		position: absolute;
		content: '\A';
    width: 100%; 
    height: 100%;
 		top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
  }
  &.no-overlay:before {
    background-color: rgba(0, 0, 0, 0);
  }
}
