// ICSC Custom Color Variables
$primary1: #e31837;
$primary2: #000;
$primary3: #fff;

// ICSC Special Colors
$secondary1: #a7a1ab;
$secondary2: #7c7cad;
$secondary3: #58a1ac;
$secondary4: #9f7b98;

$tertiary1: #f9f9f9;
$tertiary2: #f5f5f5;
$tertiary3: #eceff1;
$tertiary4: #e4eaef;
$tertiary5: #cfd8dc;
$tertiary6: #4a4a4a;
$tertiary7: #343434;

// Bootstrap Grays
$gray-crazy-dark: $tertiary7;
$gray-dark: $tertiary6;
$gray: $tertiary5;
$gray-light: $tertiary4;
$gray-lighter: $tertiary3;
$gray-lightest: $tertiary2;
$gray-ultra-light: $tertiary1;
$gray-border: $tertiary3;
$footer-gray: #3f3f3f;
$footer-gray2: #343434;

// // Reassign color vars to semantic color scheme
$brand-primary: $primary2;
// $brand-success:             $secondary3;
$brand-info: $secondary2;
// $brand-warning:             $primary1;
$brand-danger: $primary1;
$brand-inverse: $primary2;

// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
) !default;
@include _assert-ascending($grid-breakpoints, "$grid-breakpoints");
@include _assert-starts-at-zero($grid-breakpoints);

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
);
@include _assert-ascending($container-max-widths, "$container-max-widths");

// // Options
// //
// // Quickly modify global styling by enabling or disabling optional features.

$enable-rounded: false;
// $enable-shadows:            false;
$enable-gradients: true;
// $enable-transitions:        true;
// $enable-hover-media-query:  false;
// $enable-grid-classes:       true;
// $enable-print-styles:       true;

// // Spacing
// //
// // Control the default styling of most Bootstrap elements by modifying these
// // variables. Mostly focused on spacing.
// // You can add more entries to the $spacers map, should you need more variation.

// $spacer:   1rem;
// $spacer-x: $spacer;
// $spacer-y: $spacer;
$spacers: (
  0: (
    x: 0,
    y: 0,
  ),
  1: (
    x: (
      $spacer-x * 0.25,
    ),
    y: (
      $spacer-y * 0.25,
    ),
  ),
  2: (
    x: (
      $spacer-x * 0.5,
    ),
    y: (
      $spacer-y * 0.5,
    ),
  ),
  3: (
    x: $spacer-x,
    y: $spacer-y,
  ),
  4: (
    x: (
      $spacer-x * 2,
    ),
    y: (
      $spacer-y * 2,
    ),
  ),
  5: (
    x: (
      $spacer-x * 3,
    ),
    y: (
      $spacer-y * 3,
    ),
  ),
  6: (
    x: (
      $spacer-x * 5,
    ),
    y: (
      $spacer-y * 5,
    ),
  ),
  7: (
    x: (
      $spacer-x * 7,
    ),
    y: (
      $spacer-y * 7,
    ),
  ),
);

$border-width: 2px;

